module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		/* config */




		// config.baseURL = 'http://192.168.1.106:3000'; /*  */
		config.baseURL = 'http://124.221.144.134:3000'; /*  */


		return config
	})

	// 
	uni.$u.http.interceptors.response.use((response) => {
		const data = response.data
		if (data.status == 300) {
			// this.$refs.uToast.show({
			// 	type: 'error',
			// 	message: "!"
			// })
			setTimeout(() => {
				uni.redirectTo({
					url: '../login/login'
				})
			}, 2000)
		} else if (data.status == 500) {
			// this.$refs.uToast.show({
			// 	type: 'error',
			// 	message: "!"
			// })
		}
		return response
	}, (response) => {
		//  statusCode !== 200
		// return Promise.reject(response)
	})
}
